import React from 'react';
import { Link } from 'react-router-dom';

const HowToPlay = () => (
  <div className="how-to-play">
    <p className="instruction">
      1. You make the move first. <br />
      2. To make a move click on the symbol you have chosen. <br />
      3. Wait for the computer choice. <br />
      4. You and computer take turns until the score one of us reaches 3.
    </p>
    <Link to="/" className="start-btn">
      Continue game
    </Link>
  </div>
);

export default HowToPlay;
