import { UPDATE_WINNER, UPDATE_CHOICE, WINNER_IS_LOADING } from './types';

export function updateWinner(scoreData) {
  return {
    type: UPDATE_WINNER,
    scoreData,
  };
}

export function updateChoice(choiceData) {
  return {
    type: UPDATE_CHOICE,
    choiceData,
  };
}

export function winnerIsLoading(bool) {
  return {
    type: WINNER_IS_LOADING,
    isLoading: bool,
  };
}

function calculateScore(fetchData, gameData) {
  const { user, computer } = gameData;
  const choice = fetchData[user.choice][computer.choice];

  return {
    user: {
      score: choice === 'win' ? user.score + 1 : user.score,
    },
    computer: {
      score: choice === 'lose' ? computer.score + 1 : computer.score,
    },
    userGameStatus: choice,
  };
}

export function winnerFetchData(url) {
  return (dispatch, getState) => {
    dispatch(winnerIsLoading(true));
    fetch(url)
      .then(response => {
        if (!response.ok) {
          throw Error(response.statusText);
        }
        dispatch(winnerIsLoading(false));
        return response;
      })
      .then(response => response.json())
      .then(fetchData => {
        dispatch(updateWinner(calculateScore(fetchData, getState().game)));
      })
      .catch(() => console.log('error'));
  };
}
