import React from 'react';
import { string, func } from 'prop-types';

const Symbol = props => {
  const { symbol, clickHandler } = props;

  return (
    <button
      type="button"
      className="symbol"
      onClick={clickHandler}
      data-value={symbol}
    >
      <img src={`/img/${symbol.toLowerCase()}.svg`} alt={symbol} />
      <span>{symbol}</span>
    </button>
  );
};

Symbol.propTypes = {
  symbol: string.isRequired,
  clickHandler: func.isRequired,
};

export default Symbol;
