import React from 'react';
import { string, bool } from 'prop-types';

const GameStatus = props => {
  const { userGameStatus, isGameOver } = props;
  let status;

  if (isGameOver) {
    status = `Game Over. Winner is ${
      userGameStatus === 'lose' ? 'Computer' : 'User'
    }`;
  } else if (userGameStatus) {
    status = userGameStatus === 'draw' ? 'Draw!' : `You ${userGameStatus}`;
  } else {
    status = '';
  }

  return <h4>{status}</h4>;
};

GameStatus.propTypes = {
  userGameStatus: string.isRequired,
  isGameOver: bool.isRequired,
};

export default GameStatus;
