import React, { Component } from 'react';
import { connect } from 'react-redux';
import constants from '../helpers/constants';
import Board from './board';
import GameStatus from './gameStatus';
import PlayersChoice from './playersChoice';
import Preloader from './preloader';
import { updateChoice, winnerFetchData } from '../actions/winnerActions';
import startGame from '../actions/startGame';

class Game extends Component {
  handleClick = e => {
    const choiceUser = e.currentTarget.getAttribute('data-value');
    const choiceComputer = constants.symbols[Math.floor(Math.random() * 15)];

    this.props.updateChoice({
      user: {
        choice: choiceUser,
        score: 2,
      },
      computer: {
        choice: choiceComputer,
        score: 2,
      },
    });
    this.props.winnerFetchData(constants.url);
  };

  isGameOver = () => {
    const { user, computer } = this.props.game;

    return (
      user.score === constants.maxScore || computer.score === constants.maxScore
    );
  };

  render() {
    const { user, computer, userGameStatus, isLoading } = this.props.game;
    const hiddenClass = this.isGameOver() ? 'hidden' : '';

    return (
      <div className="game">
        <h1 className="score">
          Score: {user.score} - {computer.score}
        </h1>
        <div className={`game-board ${hiddenClass}`}>
          <Board clickHandler={this.handleClick} symbols={constants.symbols} />
        </div>
        <div className="game-info">
          <PlayersChoice
            hiddenClass={hiddenClass}
            userChoice={user.choice}
            computerChoice={computer.choice}
          />
          {isLoading ? (
            <Preloader isLoading={isLoading} />
          ) : (
            <GameStatus
              userGameStatus={userGameStatus}
              isGameOver={this.isGameOver()}
            />
          )}
        </div>
        <div>
          <button
            type="button"
            className={`start-btn ${this.isGameOver() ? '' : 'hidden'}`}
            onClick={this.props.startGame}
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ game }) => ({
  game,
});

function mapDispatchToProps(dispatch) {
  return {
    startGame: () => dispatch(startGame()),
    updateChoice: choiceData => dispatch(updateChoice(choiceData)),
    winnerFetchData: url => dispatch(winnerFetchData(url)),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Game);
