import React from 'react';
import { func, array } from 'prop-types';
import Symbol from './symbol';

const Board = props => {
  const { clickHandler, symbols } = props;

  const renderSymbol = symbol => (
    <Symbol clickHandler={clickHandler} symbol={symbol} />
  );
  const boardSymbols = symbols.map(item => <>{renderSymbol(item)}</>);

  return <>{boardSymbols}</>;
};

Board.propTypes = {
  clickHandler: func.isRequired,
  symbols: array.isRequired,
};
export default Board;
