import React from 'react';

const PlayersChoice = props => {
  const choiceInfo = (
    <div className={`choice-info ${props.hiddenClass}`}>
      <h2>You: {props.userChoice}</h2>
      <h2>Computer: {props.computerChoice}</h2>
    </div>
  );

  return choiceInfo;
};

export default PlayersChoice;
