import {
  UPDATE_WINNER,
  START_GAME,
  UPDATE_CHOICE,
  WINNER_IS_LOADING,
} from '../actions/types';

const initialState = {
  user: {
    choice: null,
    score: 0,
  },
  computer: {
    choice: null,
    score: 0,
  },
  userGameStatus: '',
  isLoading: false,
};

// const initialState2 = {
//   choice: [],
//   score: [0, 0],
// };

export default (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_CHOICE:
      return {
        ...state,
        user: {
          ...state.user,
          choice: action.choiceData.user.choice,
        },
        computer: {
          ...state.computer,
          choice: action.choiceData.computer.choice,
        },
      };

    case UPDATE_WINNER:
      return {
        ...state,
        user: {
          choice: state.user.choice,
          score: action.scoreData.user.score,
        },
        computer: {
          choice: state.computer.choice,
          score: action.scoreData.computer.score,
        },
        userGameStatus: action.scoreData.userGameStatus,
      };

    case START_GAME:
      return initialState;

    case WINNER_IS_LOADING:
      return { ...state, isLoading: action.isLoading };
    default:
      return state;
  }
};
